<template>
	<div>
	  <section class="container clearfix" v-for="(indexFloor,index) in indexFloorsData" :key="index">
		  <div class="layer-title">
			  <label class="aider1"><em></em> {{indexFloor.CLASSIFYS}} </label>
			  <h2>
				  <div class="link">
					  <span v-for="(link,index) in indexFloor.ABOATLINKS"><a :href="link.url">{{link.name}}</a></span>
				  </div>
			  </h2>
		  </div>
		  <div class="layer pd0 clearfix">
			  <div class="layer-inner">
				  <ul>
					  <li v-for="(good,index) in indexFloor.GOODS" :class="index==(indexFloor.GOODS.length-1)?'last':''">
						  <a :href="'./item_details.html?id='+good.GOODID">
							  <div class="goods-inner">
								  <div class="img-wrapper">
									  <img :src="good.GOODSIMAGE">
								  </div>
								  <h2 class="goods-hd">
									  {{good.GOODSNAME}}
								  </h2>
								  <div class="extra">
									  <span class="yuan">￥</span>
									  <span class="price"> {{good.PRICE}}</span>
									  <span class="letter">已售：{{good.SELLEDNUM}}件</span>
								  </div>
							  </div>
						  </a>
					  </li>
				  </ul>
			  </div>
		  </div>
	  </section>
	</div>
</template>

<style>
</style>

<script>
  import axios from 'axios';
  export default {
	name:'indexFloors',
    data() {
      return {
		  indexFloorsData: []
      };
    },
    methods: {
		getIndexFloorsData() {
			var vm = this;
			axios.post('/index_floors_data.json',{
			
			}).then((response)=>{
				 if(response.status === 200){
				 	vm.indexFloorsData = response.data;
				 	console.log(vm.indexFloorsData);
				}else{
				 	console.log("楼层数据加载失败!");
				 }
			})
		}
    },
	mounted() {
		//获取首页楼层数据
		this.getIndexFloorsData();
    }
  };
</script>